import React from "react";

import { Typography, Paper, TextField, Grid } from "@material-ui/core";
import Instruments from "./Instruments";

import BilingualTextInput from "./BilingualTextInput";

import { En, Fr, I18n } from "../I18n";

const PlatformTab = ({
  disabled,
  record,
  handleInputChange,
  paperClass,
  paperClassValidate,
}) => {
  return (
    <div>
      <Paper style={paperClass}>
        <Grid container direction="column" spacing={3}>
          <Grid item xs style={paperClassValidate("platformName")}>
            <Typography>
              <En>
                What is the name of the platform? Eg this may be a glider, ship,
                buoy, or satellites used in data collection.
              </En>
              <Fr>
                Quel est le nom de la plate-forme? Par exemple, il peut s'agir
                d'un planeur, d'un navire, d'une boue ou de satellites utiliss
                pour la collecte de donnes.
              </Fr>
            </Typography>
            <TextField
              label={<I18n en="Name" fr="Nom" />}
              name="platformName"
              value={record.platformName}
              onChange={handleInputChange}
              fullWidth
              disabled={disabled}
            />
          </Grid>
          <Grid item xs style={paperClassValidate("platformID")}>
            <Typography>
              <En>
                What is platform ID? This is a unique identification of the
                platform. If the platform is registered with ICES, use that
                identifier
              </En>
              <Fr>
                Qu'est-ce que l'ID de plate-forme ? Il s'agit d'une
                identification unique de la plateforme. Si la plateforme est
                enregistre auprs du CIEM, utilisez cet identificateur
              </Fr>
            </Typography>
            <TextField
              label={<I18n en="Platform ID" fr="ID de plateforme" />}
              name="platformID"
              value={record.platformID}
              onChange={handleInputChange}
              fullWidth
              disabled={disabled}
            />
          </Grid>

          <Grid item xs style={paperClassValidate("platformAuthority")}>
            <Typography>
              <En>
                What is the naming authority of the platform? This is the entity
                that issued the unique platform ID, eg ICES.
              </En>
              <Fr>
                Quelle est l'autorit de nommage de la plateforme ? C'est
                l'entit qui met l'ID de plate-forme unique, par exemple ICES
              </Fr>
            </Typography>
            <TextField
              value={record.platformAuthority}
              name="platformAuthority"
              onChange={(e) => handleInputChange(e)}
              fullWidth
              disabled={disabled}
            />
          </Grid>

          <Grid item xs style={paperClassValidate("platformDescription")}>
            <Typography>
              <En>Describe the platform</En>
              <Fr>Dcrire la plateforme</Fr>
            </Typography>
            <BilingualTextInput
              name="platformDescription"
              value={record.platformDescription}
              onChange={handleInputChange}
              multiline
              disabled={disabled}
            />
          </Grid>
        </Grid>
      </Paper>

      <Grid item xs>
        <Paper style={paperClass}>
          <Typography variant="h5">Instruments</Typography>
          <Typography>
            <En>Add instruments associated with this platform.</En>
            <Fr>Ajoutez des instruments associs  cette plateforme.</Fr>
          </Typography>
        </Paper>
        <Instruments
          value={record.instruments || []}
          onChange={handleInputChange}
          name="instruments"
          disabled={disabled}
          paperClass={paperClass}
        />
      </Grid>
    </div>
  );
};
export default PlatformTab;
