import React from "react";
import { Paper, Typography } from "@material-ui/core";
import { En, Fr } from "../I18n";

import BilingualTextInput from "./BilingualTextInput";
import SelectInput from "./SelectInput";
import RequiredMark from "./RequiredMark";

const MetadataTab = ({
  disabled,
  record,
  handleInputChange,
  paperClassValidate,
  paperClass,
}) => (
  <div>
    <Paper style={paperClassValidate("language")}>
      <Typography>
        <En>What is primary language of the dataset?</En>
        <Fr>Quelle est la langue principale du jeu de donnes?</Fr>
        <RequiredMark />
      </Typography>
      <SelectInput
        name="language"
        value={record.language}
        onChange={(e) => handleInputChange(e)}
        options={["en", "fr"]}
        optionLabels={["English", "Franais"]}
        disabled={disabled}
      />
    </Paper>
    <Paper style={paperClassValidate("license")}>
      <Typography>
        <En>
          How is the resource licensed? <RequiredMark />
          <br />
          <br />
          We recommend that the Creative Commons Attribution 4.0 International
          licence (CC-BY 4.0) be applied as the default licence for all datasets
          that will be made available through the CIOOS platform. This allows
          for open sharing and adaptation of the data provided that the original
          creator is attributed. Releasing the data as public domain (Creative
          Commons 0), which imposes no restrictions of any kind is also
          acceptable. For datasets made available by Government of Canada
          departments and agencies, the Open Government Licence - Canada can be
          used, which is very similar to CC-BY as it allows for open sharing and
          adaptation of the data provided that the original creator of the data
          is properly attributed.
        </En>
        <Fr>Comment la ressource est-elle sous licence?</Fr>
      </Typography>
      <SelectInput
        name="license"
        value={record.license}
        onChange={(e) => handleInputChange(e)}
        optionLabels={[
          "Creative Commons Attribution 4.0",
          "Creative Commons 0",
          "Open Government Licence - Canada",
          "Apache License, Version 2.0",
        ]}
        options={[
          "CC-BY-4.0",
          "CC0",
          "government-open-license-canada",
          "Apache-2.0",
        ]}
        disabled={disabled}
      />
    </Paper>
    <Paper style={paperClass}>
      <Typography>
        <En>
          What are the limitations affecting the fitness for use of the resource
          or metadata? Eg: <i>Appropriate credit must be given to ...</i>
        </En>
        <Fr>
          Quelles sont les limites qui influent sur l'aptitude  l'utilisation
          de la ressource ou des mtadonnes?
        </Fr>
      </Typography>
      <BilingualTextInput
        name="limitations"
        value={record.limitations}
        onChange={handleInputChange}
        multiline
        disabled={disabled}
      />
    </Paper>

    <Paper style={paperClass}>
      <Typography>
        <En>Are there any additional comments about this dataset?</En>
        <Fr>
          Y a-t-il des commentaires supplmentaires  propos de cet ensemble de
          donnes?
        </Fr>
      </Typography>
      <BilingualTextInput
        name="comment"
        value={record.comment}
        onChange={handleInputChange}
        multiline
        disabled={disabled}
      />
    </Paper>

    <Paper style={paperClass}>
      <Typography>
        <En>Describe the history of this dataset.</En>
        <Fr>Dcrivez l'historique de ce jeu de donnes.</Fr>
      </Typography>
      <BilingualTextInput
        name="history"
        value={record.history}
        onChange={handleInputChange}
        multiline
        disabled={disabled}
      />
    </Paper>
  </div>
);

export default MetadataTab;
